import { mapState } from 'vuex';
import Eye from '../../../assets/icons/eye.svg';
export default {
  components: {
    Eye
  },
  computed: {
    ...mapState(['wallets', 'activeWalletId']),
    wallet: function () {
      return this.wallets.find(wallet => wallet.id === this.activeWalletId);
    },
    seedList: function () {
      return this.wallet.mnemonic.split(' ');
    }
  }
};